$orange: #FF7043;
$white: #ffffff;
$red: #C62828;
$blue: #5C6BC0;
$greyUp: #E0E0E0;
$greyDown: #F5F5F5;
$black: #000000;
$hoverColor: #37474f;

a {
   text-decoration: none;
   color: $white;
}

header {
   display: flex;
   flex-direction: column;

   div {
      margin: 0 auto;
      text-align: center;
      padding: 5%;
   }

   ul {
      display: flex;
      justify-content: space-around;
      padding: 0;
      margin: 0;

      li {
         width: 100%;
         text-align: center;
         text-decoration: none;
         list-style: none;
         padding-top: 2%;
         padding-bottom: 2%;

         a {
            text-decoration: none;
            color: $hoverColor;
         }
      }

      li:hover {
         background-color: $hoverColor;

         a {
            text-decoration: none;
            color: $white;
         }
      }
   }
}

.region-top {
   display: flex;
   flex-direction: column;

   .title_article {
      margin: 3%;
   }

   .tags {
      color: $white;
      font-weight: lighter;
      font-size: 14px;
      margin-left: 3%;
   }

   .article-left {
      background-color: $orange;
      color: $white;

      div {
         padding: 3%;
      }
   }

   .article-right {
      display: flex;
      flex-direction: column;

      .article-right-1 {
         background-color: $red;
      }

      .article-right-2 {
         background-color: $blue;
      }
   }
}

.region-main {
   background-color: $greyUp;
   display: flex;
   flex-direction: column;
   padding: 5%;

   div {
      padding: 3%;
   }
}

.region-sidebar {
   display: flex;
   flex-direction: column;
   padding: 5%;
   background-color: $greyDown;

   form {
      display: flex;
      flex-direction: column;

      label {
         margin: 1%;
      }

      input {
         margin: 1%;
      }

      input[type=submit] {
         background-color: $blue;
         border-radius: 2px;
         border: 1px solid transparent;
         color: $white;
         width: 100px;
         margin: 5% auto;
         padding: 5px;
      }
   }
}

footer {
   height: 50px;
   background-color: $black;
}


